.main-navigation {
  position: relative;
}

.main-navigation__toggle {
  position: absolute;
  top: -58px;
  right: -9px;
  z-index: 2;
  display: block;
  width: 61px;
  height: 51px;
  background-color: inherit;
  cursor: pointer;
  border: none;
  transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
}
.main-navigation__toggle:hover, .main-navigation__toggle:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}

.main-nav--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 14px;
  left: 28px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-nav--closed .main-navigation__toggle:active::before {
  opacity: 0.3;
}

.main-nav--opened .main-navigation__toggle {
  top: -58px;
  right: -18px;
}
.main-nav--opened .main-navigation__toggle::before, .main-nav--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 18px;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-navigation__toggle:active::before, .main-nav--opened .main-navigation__toggle:active::after {
  opacity: 0.5;
}

.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .main-navigation__list {
    display: flex;
  }
}

@media (max-width: 767px) {
  .main-nav--closed .main-navigation__item {
    display: none;
  }

  .main-nav--nojs .main-navigation__toggle {
    display: none;
  }

  .main-nav--opened .main-navigation__list {
    position: absolute;
    top: 0;
    left: -20px;
    right: 0;
    z-index: 10;
    min-width: 100vw;
    background-color: #ffffff;
  }

  .main-nav--nojs .main-navigation__list {
    position: static;
    min-height: 0;
  }

  .main-nav--nojs .main-navigation__item {
    display: block;
  }
}
